  /**
   * Generate a hidden input field for the form which holds the value of the Element
   * @param  {string} name The name of the element (eg. Name,  Gender, Age etc.)
   * @param  {string?} value The Value which will returned when the form is submitted.
   */
  setHiddenInput(name, value = null) {
    if (value)
      this.innerHTML += `<input type="hidden" name="${name}" value="${value}">`;
    else this.innerHTML += `<input type="hidden" name="${name}">`;

    this.hiddenInput = this.getElementsByTagName("input")[0];
  }

  /**
   * Update the hidden input of this control
   * @param  {HTMLElement} shadowDOMInput the inner control inside the shadowDom.
   */
  updateDOM(shadowDOMInput) {
    this.hiddenInput.value = shadowDOMInput.value;
    this.value = shadowDOMInput.value;
    this.setAttribute("value", this.value);
    this.dispatchEvent(new Event("input"));
    this.dispatchEvent(new Event("change"));
  }

  /**
   *  Initialise a control.
   * @returns {HTMLElement} element which contains the control
   */
  initNativeNumberControl() {
    let elem = this.initDefaultControl();

    if (this.attr.unit) {
      let wrapper = document.createElement("div");
      let unit = document.createElement("span");

      wrapper.className = "input-unit-wrapper";
      unit.className = "unit";
      unit.innerText = this.attr.unit;

      if (this.getAttribute("name") === "unit-left") {
        unit.className += " unit-left";
      } else {
        elem.classList.add("pr-" + this.getAttribute("unit").length);
      }

      wrapper.appendChild(elem);
      wrapper.appendChild(unit);

      return wrapper;
    }

    return elem;
  }

  /**
   *  Initialise a control.
   * @returns {HTMLElement} element which contains the control
   */
  initDefaultControl() {
    let elem;
    let type = this.attr.type;
    let value = this.attr.value;

    this.setHiddenInput(this.attr.name, value);
    //Generate the control
    elem = document.createElement("input");
    elem.disabled = this.attr.disabled;
    elem.setAttribute("type", type);
    if (type == "number") {
      elem.classList.add("text-end");
    }
    elem.setAttribute("value", value);
    if (this.attr.required) {
      elem.setAttribute("required", "true");
    }
    if (this.attr.min) {
      elem.setAttribute("min", this.attr.min);
    }
    if (this.attr.max) {
      elem.setAttribute("max", this.attr.max);
    }
    if (this.readonly) {
      elem.setAttribute("readonly", "true");
    }

    elem.id = "internal-input";

    // elem.addEventListener("change", (e) => {
    //     this.dispatchEvent(new Event("change"));
    // });
    return elem;
  }
  //#endregion

  //#region Helpmethods

  /**
   * Verify a given control and checks additional attributes like min, max etc.
   * It also sets additonal information in fieldinfo.
   * @param  {HTMLElement} element The Control you want to check.
   */
  verify(element) {
    const { shadowRoot } = this;
    const checkValidity = element.checkValidity();

    let fieldinfo = shadowRoot.querySelector(".fieldinfo");

    var row = fieldinfo.querySelectorAll("span");
    var minFailed,
      maxFailed = false;

    if (
      !checkValidity &&
      element.getAttribute("min") == null &&
      element.getAttribute("max") == null
    ) {
      const msg = element.validationMessage;
      const length = msg.length;
      row[0].innerText = msg.substring(0, Math.ceil(length / 2));
      row[0].classList.add("red");
      row[1].innerText = msg.substring(Math.ceil(length / 2), length);
      row[1].classList.add("red");
      minFailed = true;
      maxFailed = true;
    }

    if (element.getAttribute("min") != null) {
      if (element.value < parseFloat(element.getAttribute("min"))) {
        minFailed = true;
        if (!this.getAttribute("disableSoftValidation")) {
          row[0].classList.add("red");
        }
      } else row[0].classList.remove("red");
    }

    if (element.getAttribute("max") != null) {
      if (element.value > parseFloat(element.getAttribute("max"))) {
        maxFailed = true;
        if (!this.getAttribute("disableSoftValidation")) {
          row[1].classList.add("red");
        }
      } else row[1].classList.remove("red");
    }

    if (minFailed || maxFailed) {
      if (this.getAttribute("disableSoftValidation") != null ? true : false) {
        element.value =
          element.value < element.getAttribute("min")
            ? element.getAttribute("min")
            : element.value;
        element.value =
          element.value > element.getAttribute("max")
            ? element.getAttribute("max")
            : element.value;
      } else {
        element.classList.add("red-border");
      }
    } else {
      element.classList.remove("red-border");
    }

    if (!minFailed && !maxFailed) {
      row[0].innerText = "";
      row[1].innerText = "";
    }
  }

  //#endregion

  //#region methods Checkbox

  /**
   * Initialise a checkbox control.
   */
  initCheckboxControl() {
    this.setHiddenInput(this.attr.name);
    return this.generateCheckbox(this.attr.name, this.attr.value);
  }

  /**
   * Help method to generate the HTML
   * @param  {string} name The name of the element (eg. approve, delete etc.)
   * @param  {string} value  The Value which will returned when the Checkbox is checked.
   */
  generateCheckbox(name, value) {
    var val = value == "" ? "on" : value;

    let checkedString = "";
    if (this.getAttribute("checked") != null) {
      checkedString = "checked";
    }

    var checkboxContainer = document.createElement("div");
    checkboxContainer.classList.add("hmi-checkbox-container");
    checkboxContainer.innerHTML = `
            <input ${this.disabledString} ${
      this.requiredString
    }  ${checkedString}
                type="checkbox" id='internal-input' name="${name}" value="${val}"
                ${this.readonly && 'class="readonly" onclick="return false;"'}>
            <span class="checkmark"></span>
        `;

    return checkboxContainer;
  }

