window.toggleHmiMenu = function (button) {
    var menu = document.getElementsByClassName("main-nav")[0];
    button.classList.toggle("clicked");
    menu.classList.toggle("hidden");
}

window.collapse = function(src) {
    var submenu = src.nextElementSibling;
    var spanElem = src.children[0];
    submenu.classList.toggle("hidden");
    spanElem.classList.toggle("icon-down");
    spanElem.classList.toggle("icon-up");
}

window.registerDotnetObjectReference = function(dotnetObjectReference) {
    window.dotNetHelper = dotnetObjectReference;
};

window.openUserMenu = () => {

    window.closeHMIMenu();

    const op_column = document.getElementById("operator_coloumn");
    const op_column_close = document.getElementById("operator_coloumn_close");

    const usermenu =  document.getElementById("usermenu");
    usermenu.classList.toggle("visible");

    if(usermenu.classList.contains("visible")) {
      setTimeout( () => {
        op_column_close.classList.toggle("hidden");
      }, 10);
     

      const backdropelem = document.createElement("div");
      backdropelem.classList = "modal-backdrop show";
      document.body.append(backdropelem);
    } else {
      op_column_close.classList.toggle("hidden");
       setTimeout( () => 
        {
          document.getElementsByClassName("modal-backdrop show")[0].remove();
        }, 500);        
    }
  }

window.closeHMIMenu = ()  => {
    const hmiMenuButton = document.getElementsByClassName("hmi-btn-menu")[0];

    if(hmiMenuButton.classList.contains("clicked")) {
      hmiMenuButton.click();
    }
  }

//#region  HMI Custom selects
var x, i, j, l, ll, selElmnt, a, b, c;
/* Look for any elements with the class "hmi-select": */
var downIcon = '<button type="button" class="float-end"><span class="icon icon-down-dark float-end"></span></button>';
x = document.getElementsByClassName("hmi-select");
l = x.length;
for (i = 0; i < l; i++) {
selElmnt = x[i].getElementsByTagName("select")[0];
ll = selElmnt.length;
/* For each element, create a new DIV that will act as the selected item: */
a = document.createElement("DIV");
a.setAttribute("class", "select-selected");
a.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML + downIcon;
x[i].appendChild(a);
/* For each element, create a new DIV that will contain the option list: */
b = document.createElement("DIV");
b.setAttribute("class", "select-items select-hide");
for (j = 1; j < ll; j++) {
    /* For each option in the original select element,
    create a new DIV that will act as an option item: */
    c = document.createElement("DIV");
    c.innerHTML = selElmnt.options[j].innerHTML;
    c.addEventListener("click", function(e) {
        /* When an item is clicked, update the original select box,
        and the selected item: */
        var y, i, k, s, h, sl, yl;
        s = this.parentNode.parentNode.getElementsByTagName("select")[0];
        sl = s.length;
        h = this.parentNode.previousSibling;
        for (i = 0; i < sl; i++) {
        if (s.options[i].innerHTML == this.innerHTML) {
            s.selectedIndex = i;
            h.innerHTML = this.innerHTML + downIcon;
            y = this.parentNode.getElementsByClassName("same-as-selected");
            yl = y.length;
            for (k = 0; k < yl; k++) {
            y[k].removeAttribute("class");
            }
            this.setAttribute("class", "same-as-selected");
            break;
        }
        }
        h.click();
    });
    b.appendChild(c);
}
x[i].appendChild(b);
if(selElmnt.hasAttribute("disabled"))
    continue;
a.addEventListener("click", function(e) {
    /* When the select box is clicked, close any other select boxes,
    and open/close the current select box: */
    e.stopPropagation();
    closeAllSelect(this);
    this.nextSibling.classList.toggle("select-hide");
    this.classList.toggle("select-arrow-active");
});
}

function closeAllSelect(elmnt) {
/* A function that will close all select boxes in the document,
except the current select box: */
var x, y, i, xl, yl, arrNo = [];
x = document.getElementsByClassName("select-items");
y = document.getElementsByClassName("select-selected");
xl = x.length;
yl = y.length;
for (i = 0; i < yl; i++) {
    if (elmnt == y[i]) {
    arrNo.push(i)
    } else {
    y[i].classList.remove("select-arrow-active");
    }
}
for (i = 0; i < xl; i++) {
    if (arrNo.indexOf(i)) {
    x[i].classList.add("select-hide");
    }
}
}

/* If the user clicks anywhere outside the select box,
then close all select boxes: */
document.addEventListener("click", closeAllSelect);
//#endregion

function numControl(element, val, decimals=2) {
    var input = element.parentNode.querySelector('input');
    input.value = (parseFloat(input.value) + parseFloat(val)).toFixed(decimals);
 }

 window.elementHeightByClassName = function(className) {
     return document.getElementsByClassName(className)[0].clientHeight;
 }

 
